const mongoose = require('mongoose');
const { ObjectId } = mongoose.Schema;

const DataColRoomSchema = mongoose.Schema({
    user: {
        type: ObjectId,
        ref: 'User',
    },

    date: {
        type: Date,
        default: Date.now(),
    },
});



const model = mongoose.model('DataColRoom', DataColRoomSchema);

export default model;
